var app = getApp();
var core = require('../../utils/core.js')

// pages/auth/auth.js
Page({

  /**
   * 
   */
  data: {
    shop_logo: '',
    shop_name: ''
  },

  /**
   * --
   */
  onLoad: function (options) {
    
    core.get('wxAppSetting', {}, (response) => {
      var sysSet = response.sysset
      
      this.setData({
        shop_logo: sysSet.shoplogo,
        shop_name: sysSet.shopname
      })
    })
  },

  /**
   * --
   */
  onReady: function () {

  },

  /**
   * --
   */
  onShow: function () {

  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {

  },

  /**
   * --
   */
  onPullDownRefresh: function () {

  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  },
  bindGetUserInfo: function (res) {
    wx.login({
      success: function (ret) {
        core.post('wxapp/login', { code: ret.code }, function (login_res) {
          if (login_res.error) {
            core.alert(':' + login_res.message);
            return;
          }
          core.get('wxapp/auth', {
            data: res.detail.encryptedData,
            iv: res.detail.iv,
            sessionKey: login_res.session_key
          }, function (auth_res) {
            if (auth_res.isblack == 1) {
              wx.showModal({
                title: '',
                content: '',
                success: function (res) {
                  if (res.confirm) {
                    app.close();
                  }
                  if (res.cancel) {
                    app.close();
                  }
                }
              })
            }
            res.detail.userInfo.openid = auth_res.openId
            res.detail.userInfo.id = auth_res.id
            res.detail.userInfo.uniacid = auth_res.uniacid
            app.setCache('userinfo', res.detail.userInfo);
            app.setCache('userinfo_openid', res.detail.userInfo.openid);
            app.setCache('userinfo_id', auth_res.id);
            app.getSet();
            wx.reLaunch({
              url: '/pages/index/index'
            })
          })
        })
      },
      fail: function () {
        core.alert('!');
      }
    })
  },
  close () {
    wx.navigateBack({

      delta: 0

    })
  }
})