var app=getApp(),core=app.requirejs('core'),$=app.requirejs('jquery');
Page({
    data: {
        search: false,
        show_distance:false
    },
    onLoad: function (options) {
        //  options
        this.setData({
            options: options
        });
        app.url(options);
    },
    onReady: function () { 
        // 
    },
    onShow: function () { 
        // 
      this.get_list();
    },
    onHide: function () {
        // 
    },
    get_list: function () {
      var $this = this;
      var obj = { 
        ids: $this.data.options.ids, 
        type: $this.data.options.type, 
        merchid: $this.data.options.merchid 
      }; 
      wx.getSetting({
        success: function (res) {  
          var accredit = res.authSetting['scope.userLocation'];
          if (accredit){
            wx.getLocation({
              type: 'wgs84',
              success: function (res) { 
                obj.lat = res.latitude;
                obj.lng = res.longitude;
                $this.setData({ show_distance: true })
                core.get('store/selector', obj, function (result) {
                  $this.setData({ list: result.list, show: true });
                });
              },
              fail: function (ret) {  
                setTimeout(function () {
                  core.toast("");
                }, 1000);
                core.get('store/selector', obj, function (result) {
                  $this.setData({ list: result.list, show: true });
                });
              }
            })
          }else{  
            wx.authorize({
              scope: 'scope.userLocation',
              success: function () {
                wx.getLocation({
                  type: 'wgs84',
                  success: function (res) { 
                    obj.lat = res.latitude;
                    obj.lng = res.longitude;
                    $this.setData({ show_distance: true })
                    core.get('store/selector', obj, function (result) {
                      $this.setData({ list: result.list, show: true });
                    });
                  },
                  fail: function (ret) {  
                    setTimeout(function () {
                      core.toast("");
                    }, 1000);
                    core.get('store/selector', obj, function (result) {
                      $this.setData({ list: result.list, show: true });
                    });
                  }
                })
              },
              fail: function (ret) { 
                
                var accredit = res.authSetting['scope.userLocation'];
                if (accredit){
                  wx.showModal({
                    title: '',
                    content: ',',
                    success(res) {
                      if (res.confirm) {
                        wx.openSetting({
                          success(res) {
                            wx.navigateBack();
                          }
                        })
                      }
                      if (res.cancel) {
                        app.close();
                      }
                    }
                  })
                } 
              }
            })
          }
        }
      });   
    },
    bindSearch: function (e) {
        this.setData({
            search: true
        })
    },
    phone:function(e){
        core.phone(e);
    },
    select: function (e) {
        var index = core.pdata(e).index;
        app.setCache("orderShop", this.data.list[index], 30);
        wx.navigateBack();
    },
    search: function (e) {
        var val = e.detail.value,old_list=this.data.old_list,list=this.data.list,new_list=[];
        if ($.isEmptyObject(old_list)){
            old_list = list;
        }
        if (!$.isEmptyObject(old_list)){
            $.each(old_list,function (index,item) {
                if (item.storename.indexOf(val)!=-1){
                    new_list.push(item);
                }
            })
        }
        this.setData({list:new_list,old_list:old_list});
    }
})