var app = getApp();
var core = app.requirejs('core');
var foxui = app.requirejs('foxui');
Page({
  data: {
  	accredit: '',
  	errMsg: '',
  	Image: 'https://api.clubmall.cn/attachment/images/7/2017/11/r13oT11buG60bn2ntVp1q4pe3B6EGQ.jpeg'
  },
  
  onLoad: function (options) {
  
  },
	/**/
	previewImage: function(){
		wx.previewImage({
		  current: 'https://api.clubmall.cn/attachment/images/7/2017/11/r13oT11buG60bn2ntVp1q4pe3B6EGQ.jpeg', // http
		  urls: ['https://api.clubmall.cn/attachment/images/7/2017/11/r13oT11buG60bn2ntVp1q4pe3B6EGQ.jpeg'] // http
		})
	},
	/**/
  savePicture: function(){
  	var $this = this;
  	wx.getSetting({
		  success: function(res){
		  	var accredit = res.authSetting['scope.writePhotosAlbum']
		  	if(accredit){
		  		wx.showLoading({
					  title: '...',
					})
		  		setTimeout(function(){
					  wx.hideLoading()
					},1000)
		  		wx.downloadFile({
		  			url: 'https://api.clubmall.cn/attachment/images/7/2017/11/r13oT11buG60bn2ntVp1q4pe3B6EGQ.jpeg',
		  			success: function(res) {
  						wx.saveImageToPhotosAlbum({
				  			filePath: res.tempFilePath,
					    	success(result) {
					    		foxui.toast($this, "");
							  },
					    	fail:function(ret){
					    		$this.setData({errMsg: ret.errMsg})
					    		foxui.toast($this, "");
					    	}
							})
		  			}
		  		})
		  	}else{
		  		wx.authorize({
	          scope: 'scope.writePhotosAlbum',
			  		fail: function(){
              app.getmsg('writePhotosAlbum', '');
		  			}
		  		})
		  	}
			}
		})	
  }

})