var app = getApp(),
  core = app.requirejs('core');
Page({
  data: {
    stars_class: ['text-default', 'text-primary', 'text-success', 'text-warning', 'text-danger'],
    stars_text: ['', '', '', '', ''],
    normalSrc: 'icox icox-star',
    selectedSrc: 'icox icox-xing selected',
    key: -1,
    content: '',
    images: [],
    imgs: [],
    log:[],
    is_openmerch:0,
    shop:[],
  },
  onLoad: function (options) {
    //  options
    this.setData({
      options: options
    });
    app.url(options);
    this.get_list();
  },
  get_list: function () {
    var $this = this;
    core.get('creditshop/comment', $this.data.options, function (list) {
      if (list.error == 0) {
        list.show = true;
        $this.setData(list);
        $this.setData({
          log: list.log,
          is_openmerch: list.is_openmerch,
          shop: list.shop,
        });
      } else {
        core.toast(list.message, 'loading');
        wx.navigateBack();
      }
    }, true);
  },
  select: function (e) {
    var key = e.currentTarget.dataset.key;
    this.setData({
      key: key
    });
  },
  change: function (e) {
    var name = core.data(e).name, data = {};
    data[name] = e.detail.value;
    this.setData(data);
  },
  submit: function () {
    var $this = this, data = {
      logid: this.data.log.id,
      merchid:this.data.log.merchid,
      comments: []
    };
    if (this.data.content == '' || this.data.key == -1) {
      core.alert('!')
      return false;
    }
    var g = {
      goodsid: this.data.log.goodsid,
      level: this.data.key + 1,
      content: this.data.content,
      images: this.data.images
    };
    data.comments.push(g);
    core.post('creditshop/comment/submit', data, function (list) {
      if (list.error != 0) {
        core.toast(list.message, 'loading')
      }
      wx.navigateBack()
    }, true);
  },
  upload: function (e) {
    var $this = this, dataset = core.data(e), type = dataset.type, images = $this.data.images, imgs = $this.data.imgs, index = dataset.index;
    if (type == 'image') {
      core.upload(function (file) {
        images.push(file.filename);
        imgs.push(file.url);
        $this.setData({
          images: images,
          imgs: imgs
        })
      });
    } else if (type == 'image-remove') {
      images.splice(index, 1);
      imgs.splice(index, 1);
      $this.setData({
        images: images,
        imgs: imgs
      })
    } else if (type == 'image-preview') {
      wx.previewImage({
        current: imgs[index],
        urls: imgs
      })
    }
  }
});
